import './App.css';
import React, {useState, useEffect} from 'react'
import DisplayContent from './Components/DisplayContent'
import NavBar from './Components/NavBar'
import {Grid} from '@material-ui/core';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import Search from './Components/search';


const useStyles = makeStyles(() =>
    createStyles({
        root: {
            flexGrow: 1,
            padding: '0em 10% 0em 10%',
            minHeight: '100vh',
            backgroundColor: 'black',
        },
        items: {
            border: '1px solid green'
        }
    })
)


function App() {
  const [controls, setControls] = useState({type: 'characters', data: [], page: 1, offset: 0, showImages: false})
  const classes = useStyles();

  useEffect(async () => {
    controls.data.length === 0 && getMediaType({type: controls.type, offset: 0, page: controls.page})
  }, [])

  const getMediaType = async (props) => {
        let defaultSearch;
        props.defaultSearch === false ? defaultSearch = false : defaultSearch = true;
        setControls({...controls, type: undefined})
        const requestedMedia = await Search({type: props.type, offset: props.offset, defaultSearch: defaultSearch, url: props.url});
        await setControls({...controls, type: props.type,
                                  total: requestedMedia.total,
                                  offset: requestedMedia.offset,
                                  count: requestedMedia.count,
                                  limit: requestedMedia.limit,
                                  data: requestedMedia.results,
                                  page: props.page,
                                  url: props.url,
                                  pageHead: props.pageHead
                                });
  }

  const changeContent = async (item) => {
    console.log(item);
    await setControls({});
    getMediaType({type: controls.type, page: 1, offset: 0, defaultSearch: false, url: item.URI, pageHead: ' for ' + item.type + ' featuring ' + item.name})
  }

  const nextPage = async () => {
    {controls.pageHead === undefined ?
    await getMediaType({type: controls.type, offset: controls.offset+12, page: controls.page+1})
    :
    await getMediaType({type: controls.type, page: controls.page+1, offset: controls.offset+12, defaultSearch: false, url: controls.url, pageHead: controls.pageHead})
    }
  }
  const prevPage = async () => {
    await getMediaType({type: controls.type, offset: controls.offset-12, page: controls.page-1})
  }
  const hideImages = (props) => {
    setControls({...controls, showImages: props})
  }

  return (
    <>
    <NavBar value={getMediaType} showImages={controls.showImages} hideImages={hideImages} controls={controls} />
    {/* <SearchApi/> */}
      <div className={classes.root}> 
          <Grid container style={{display: 'flex', justifyContent: 'center'}} spacing={2}>
          {/* <Grid style={{padding: '0',}} sm={10} item><SideBar controls={controls} value={getMediaType} /></Grid> */}
          <Grid zeroMinWidth item xs={11}><DisplayContent changeContent={changeContent} nextPage={nextPage} prevPage={prevPage} controls={controls} /></Grid>
        </Grid>
      </div>
      </>
  );
}

export default App;
