import React, { useState, useEffect } from 'react'
import { createStyles, makeStyles } from '@material-ui/core/styles';
import {SkipNext, SkipPrevious } from '@material-ui/icons';
import { Card, CardActionArea, CardMedia, Grid, Typography,
         Accordion, AccordionSummary, AccordionDetails, Dialog, DialogContent,
         DialogContentText, Button
} from '@material-ui/core';
import CreateAccordion from './CreateAccordion'


const useStyles = makeStyles(() =>
    createStyles({
        root: {
            flexGrow: 1,
            color: 'white',
        },
        centerElement: {
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center'
        },
        listItem: {
            overflow: 'scroll',
            maxHeight: '10rem',
            overflowX: 'hidden',
            backgroundColor: 'lightgray',
            padding: '0', margin: '0'
        },
        accordion: {
            width: '100%',
        },
        dialogHead: {
            width: '100%',
            textAlign: 'center',
            fontSize: '24px',
            fontFamily: 'Roboto'
        },
        dialog: {
            overflow: 'hidden'
        },
        card: {
            position: 'relative',
            margin: '1em'
        },
        overlayFont: {
            position: "absolute",
            top: "40%",
            width: "100%",
            textAlign: "center",
            color: "white",
            fontFamily: "Roboto",
        },
        flexContainer: {
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            padding: 0,
            color: 'black',
            width: '100%',
        },
        pageBtn: {
            float: 'right',
            color: 'white',
            fontSize: '1em',
            padding: '0'
        }
    })
)

function DisplayContent(props) {

    const [open, setOpen] = useState(false);
    const [item, setItem] = useState({});
    const [loading, setLoading] = useState(false);
    const [loadingImg, setLoadingImg] = useState(true)

    const controls = props.controls
    const showImages = controls.showImages
    const data = controls.data
    const classes = useStyles();
    const noImage = 'http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available'
    const placeholderImage = "http://i.annihil.us/u/prod/marvel/i/mg/f/60/4c002e0305708"

    useEffect(async () => {
        { props.controls.type === undefined ? await setLoading(true) : await setLoading(false) }
    })

    useEffect(() => {
        (props.controls.url !== undefined) && handleClose();
    },[controls.url])



    const handleOpen = (props) => {
        setOpen(!open);
        setItem(props)
    }
    const handleClose = () => {
        setOpen(false);
        setItem({})
    }

    const pageOptions = () => {
        return (
            <span className={classes.pageBtn}>
                <Button className={classes.pageBtn} onClick={props.nextPage}><SkipNext fontSize="large" /></Button>
                <Button className={classes.pageBtn}>{props.controls.page}</Button>

                {props.controls.offset !== 0 &&
                    <Button className={classes.pageBtn} onClick={props.prevPage}><SkipPrevious fontSize="large" /></Button>

                }
            </span>
        )
    }


    return (
        <div className={classes.root}>
            <Typography  variant="h6">
                <span>Showing {controls.offset} - {controls.offset + 12} of {controls.total} results found {controls.pageHead}</span>
                {pageOptions()}
            </Typography>
            <Dialog open={open} onClose={handleClose} fullWidth maxWidth="xs">
                <Accordion defaultExpanded style={{ margin: '0', padding: '0' }}>
                    <AccordionSummary>
                        <Typography className={classes.dialogHead}>{item.name || item.title || item.fullName}</Typography>
                        
                    </AccordionSummary>
                    <AccordionDetails style={{ margin: '0', padding: '0' }} >
                        <DialogContent className={classes.dialog} style={{ margin: '0', padding: '0' }}>

                            <Grid container className={classes.centerElement}>
                                <Grid item md={10}>
                                    <DialogContentText>
                                        {(item.thumbnail !== undefined && item.thumbnail !== null && item.thumbnail.path !== noImage) &&
                                            <Card className={classes.card}>
                                                <CardMedia component="img"
                                                    className={classes.dialogImg}
                                                    image={item.thumbnail.path + '/standard_incredible.' + item.thumbnail.extension}
                                                    title={item.name}
                                                />
                                            </Card>

                                        }
                                    </DialogContentText>
                                </Grid>
                                <Grid item md={12}>
                                    <DialogContentText id="alert-dialog-slide-description">
                                        <Typography style={{ margin: '1em' }}>{item.description}</Typography>
                                    </DialogContentText>
                                </Grid>
                            </Grid>

                        </DialogContent>
                    </AccordionDetails>
                </Accordion>
                {CreateAccordion({type: controls.type, item: item, changeContent: props.changeContent, handleClose: handleClose})}
            </Dialog>

            <Grid container spacing={2}>
                {(data !== undefined && loading === false) ? data.map((item, index) => {
                    return (
                        <>
                            {(item.thumbnail.path !== noImage && item.thumbnail.path !== placeholderImage || showImages === false) &&
                                <Grid key={index} lg={2} md={3} sm={4} xs={6} item className={classes.items}>
                                    <Card>
                                        <CardActionArea>
                                            <CardMedia
                                                component="img"
                                                onLoad={() => setLoadingImg(true)}
                                                style={loadingImg ? {} : {display: 'relative'}}
                                                image={item.thumbnail.path + '/portrait_uncanny.' + item.thumbnail.extension}
                                                title={item.name}
                                                onClick={() => handleOpen(item)}
                                            />
                                        </CardActionArea>
                                    </Card>
                                    {/* <h4 style={{ textAlign: 'center', fontFamily: 'roboto' }}>{item.name || item.title || item.fullName}</h4> */}
                                </Grid>
                            }
                        </>
                    )
                }) :
                    //Not my code, just a placeholder 
                    <div className="loaderBody">
                        <div className="loader">
                            <span>L</span>
                            <span>O</span>
                            <span>A</span>
                            <span>D</span>
                            <span>I</span>
                            <span>N</span>
                            <span>G</span>
                        </div>
                    </div>
                }

            </Grid>
        </div>
    )
}

export default DisplayContent;

