import React, { useState } from 'react'
import { createStyles, makeStyles } from '@material-ui/core/styles';
import { ExpandLess, Visibility } from '@material-ui/icons';
import {
    Typography, ListItem, List, Button,
    Accordion, AccordionSummary, AccordionDetails
} from '@material-ui/core';


const useStyles = makeStyles(() =>
    createStyles({
        listItem: {
            overflow: 'scroll',
            maxHeight: '10rem',
            overflowX: 'hidden',
            backgroundColor: 'lightgray',
            padding: '0', margin: '0'
        },
        accordion: {
            width: '100%',
        },
    })
)

export default function CreateAccordion(props) {
    const name = props.item.name || props.item.title
    const [expanded, setExpanded] = useState(false);
    const classes = useStyles();
    const changeContent = props.changeContent

    const handleChange = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false)
    }

    const displayAccordion = (props) => {

        const type = props.type
        return (
            type.map((data, index) => (
                data.data !== undefined && (data.data.available > 0 && data.data.available <= 20) ?
                    <Accordion key={index} expanded={expanded === index} onChange={handleChange(index)} style={{ margin: '0', padding: '0' }}>
                        <AccordionSummary
                            expandIcon={<ExpandLess color="action" />}
                            style={{borderTop: '1px solid lightgrey', backgroundColor: 'grey'}}
                        >
                            <Typography className={classes.accordion} key={index} variant="h6">
                                <span>{data.name}</span>
                                <span style={{ float: 'right' }}>({data.data.available})</span>

                            </Typography>
                        </AccordionSummary>
                        <AccordionDetails className={classes.listItem}>
                            <Typography>
                                {data.data !== undefined && data.data.items.map((listItem, index) => (
                                    <AccordionDetails key={index}>
                                        <List>
                                            <ListItem button >{listItem.name}</ListItem>

                                            {/* onClick={() => changeContent({ item: listItem, type: data.name })} */}

                                        </List>
                                    </AccordionDetails>
                                ))}
                            </Typography>
                        </AccordionDetails>
                    </Accordion>
                    : (data.data !== undefined && data.data.available !== 0) &&
                    <AccordionSummary
                        expandIcon={<Visibility color="action" />}
                        style={{borderTop: '1px solid lightgrey', backgroundColor: 'grey'}}

                    >
                        <Typography onChange={props.handleClose} onClick={() => {changeContent({type: data.name, name: name, URI: data.data.collectionURI})}} className={classes.accordion} key={index} variant="h6">
                            <span>{data.name}</span>
                            <span style={{ float: 'right' }}>({data.data.available})</span>
                        </Typography>
                    </AccordionSummary>
            ))
        )
    }
    const type = props.type
    const item = props.item
    if (type !== undefined) {
        return displayAccordion
            ({
                type:
                    [
                        {
                            name: 'Comics', data: item.comics
                        },
                        {
                            name: 'Series', data: item.series
                        },
                        {
                            name: 'Stories', data: item.stories
                        },
                        {
                            name: 'Events', data: item.events
                        },
                        {
                            name: 'Characters', data: item.characters
                        }
                    ]
            })
    }

}



